<template>
  <div class="activity-posters-container">
    <div class="card-box">
      <p class="card-title-style">活动海报</p>
      <Card class="card-style">
        <div class="edit-poster-box flex-center" @click="onClick">
          <p class="text-style">编辑海报</p>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    Card: () => import("@/components/Card"),
  },
  data() {
    return {
      lid: "",
    };
  },
  created() {
    this.lid = this.$route.query.id;
  },
  methods: {
    onClick() {
      this.$router.push({
        path: `/editPoster`,
        query: {
          lid: this.lid,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.activity-posters-container {
  .card-box {
    margin-top: px(40);
    .card-title-style {
      font-size: px(36);
      font-weight: bold;
    }
    .card-style {
      margin-top: px(12);
      padding: px(24);
      .edit-poster-box {
        margin: 0 auto;
        width: px(240);
        height: px(340);
        background: #f7f7f7;
        border-radius: px(16);
        border: px(1) solid #eeeeee;
        .text-style {
          font-size: px(36);
          font-weight: bold;
          color: #cccccc;
        }
      }
    }
  }
}
</style>
